@import url('https://fonts.googleapis.com/css2?family=Montserrat&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #a7bcb9;
  color: #fff;
}

.crypto-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  color: #fff;
}

.crypto-search {
  display: flex;
  margin-bottom: 50px;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.crypto-text {
  margin-bottom: 30px;
  text-align: center;
}

.crypto-input {
  padding-left: 16px;
  width: 370px;
  height: 40px;
  border-radius: 5px;
  border: none;
  background-color: #5f5d5c;
  color: #e2e2e2;
  font-size: 1.2rem;
  cursor: pointer;
}

.crypto-input::placeholder {
  color: #e2e2e2;
}
